$color-primary : #FF6060;
$default-white: #FFFFFF;
$default-black: #000000;
$dark-grey: #2d2d2da8;
$light-grey:#F7F7F7;

$gradient-background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);


$breakpoint:(
        mobile: 768px,
        tablet: 992px,
);

@mixin mobile{
    @media all and (max-width:map-get($breakpoint,mobile)){
        @content
    }
}

@mixin tablet{
    @media all and (max-width:map-get($breakpoint,tablet)){
        @content
    }

}

