@import "../../utils/variables.scss";

footer{
    height: 209px;
    background: #000000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    margin-top: 40px;
    &img{
        margin: 0;
        width: 0;
        height: 0;
    }
}

.footer_subtitle{
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    display: flex;
    align-items: flex-end;
    text-align: center;
    margin-top: 30px;
    color: #FFFFFF;
    @include tablet {
        font-size: 14px;
    }
}

